@media screen and (min-width: 1000px) {
  img {
    max-width: 10%;
    height: auto;
    margin: "0px";
  }

  .Coin-Logo {
    min-width: 15px;
    height: 25px;
    position: relative;
    padding-left: 12px;
    top: 5px;
    right: 10px;
  }
  .symbol {
    position: relative;
    font-size: 100%;
    left: 10px;
    color: grey;
  }
  table {
    font-family: "Nunito", sans-serif;
    min-width: 90vw;
    max-width: 90vw;
    color: #F5F5F5;
    font-size: 14px;
    background: linear-gradient(rgba(250, 250, 250, 0), rgba(250, 250, 250, 0));
    border-width: 0px;
    border-style: solid;
    border-color: #FFFFFF;
    border-image: initial;
    border-radius: 25px;
    overflow: hidden;
    table-layout: fixed;
    width: fixed;
    padding: 20px;
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .chartandpay {
    table-layout: fixed;
    position: center;
  }
  tr {
    text-align: center;
    padding: 12px;

    border-width: 1px;
    border-radius: 0px;
  }
  .theCoinId:hover {
    background-color: #111111;
    box-shadow: rgba(0, 0, 0, 0) 1.5px 1.5px 3px,
      rgba(0, 0, 0, 0) -3px -3px 6px -0.5px;
    cursor: pointer;
  }

  .theCoinIdSec {
    float: left, center;
    color: #FFFFFF;
  }
  .theCoinId {
    min-width: 1vw;
  }
  chart {
    width: 30px;
    float: left;
  }

  .table-header-button {
    cursor: pointer;
  }
  .table-header-button:hover {
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0) 1.5px 1.5px 3px,
      rgba(0, 0, 0, 0) -3px -3px 6px -0.5px;
  }
  th,
  td,
  tr {
    text-align: left;
    padding: 10px;
    border-width: 1px;
    border-color: red;
    border: collapse;
    table-layout: fixed;

    border-radius: 10px;
  }
  td {
    margin: 0px solid black;

    border: collapse;
    margin: 0px;
  }

  .up {
    background-color: rgba(0, 0, 0, 0);
    border-collapse: collapse;
    border-radius: 10px;
    color: #3ADC59;
    text-align: center;
    border: 0px solid #FFFFFF;
  }
  .down {
    background-color: rgba(0, 0, 0, 0);
    border-collapse: collapse;
    border-radius: 10px;
    color: #FF0D0D;
    text-align: center;
    border: 0px solid #FF0D0D;
  }
  .crash {
    background-color: rgba(0, 0, 0, 0);
    border-collapse: collapse;
    border-radius: 10px;
    color: #FF0D0D;
    text-align: center;
    border: 0px solid rgba(255, 255, 255, 0);
  }
  iframe {
    border-radius: 10px;

    float: right;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    border: 0px solid rgb(226, 207, 213);
    min-width: 40px;
    min-height: 40px;
  }
  .page-selection {
  }
  .emoji {
    font-size: 28px;
    position: relative;

    background-color: rgba(0, 0, 0, 0);
    border: 1px solid #FFFFFF;
  }
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    width: 40vw;
    margin: 0 auto;
    min-height: 1vh;
    margin-left: 10px;
    padding-bottom: 5px;
  }
  .btn {
    flex: 1 1 auto;
    font-family: Nunito, sans-serif;
    margin-bottom: 10px;
    padding: 3px;
    text-align: center;

    transition: 0.5s;
    background-size: 200% auto;
    color: #FFFFFF;
    /* text-shadow: 0px 0px 10px rgba(0,0,0,0.2);*/
    box-shadow: 0 0 20px #191c26;
    border-radius: 10px;
    float: left;
  }

  /* Demo Stuff End -> */

  /* <- Magic Stuff Start */

  .btn:hover {
    background-color: #464EB5;
  }

  .btn-1 {
    color: #FFFFFF;
    font: 400 13.3333px Nunito;
    font-family: Nunito;
    font-weight: 700;
    padding: 7px;
    margin: 3px;
    max-width: 90px;
    background: #111111;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(226, 207, 213);
    border-image: initial;
    border-radius: 12px;
    cursor: pointer;
    overflow: hidden;
    float: left;
  }
  .btn-2 {
    color: rgb(91, 38, 57);
    font-family: "Nunito", sans-serif;
    padding: 5px;
    margin: 3px;
    max-width: 90px;
    background: #111111;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(226, 207, 213);
    border-image: initial;
    border-radius: 12px;
    cursor: pointer;
    overflow: hidden;
    float: left;
  }
  .loader {
    font-size: 50px;
    animation: spin 2s linear infinite;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
