      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  .mobData {
    padding-left: 30px;
    padding-right: 30px;
  }
  .mobHeader {
    padding-left: 30px;
    padding-right: 30px;
  }
  .additionalData {
    background-image: rgba(255, 255, 255, 0);
    border-radius: 10px;
    width: fit-content;
    height: fit-content;
    padding: 10px;
    font-size: 15px;
    background: #BFCEEA;
    border-width: 0px;
    border-style: solid;
    border-color: #FFFFFF;
    border-image: initial;
    border-radius: 12px;
    flex: 1 1 0%;
    overflow: hidden;
    align-content: center;
    max-height: 400px;
    object-fit: cover;
    display: block;
    margin-left: none;
    margin-right: none;
    margin-bottom: 25px;
    margin-top: 15px;
    align-content: center;
  }
  .infotext {
    padding-top: -6px;
    margin-top: 3px;
    text-align: center;
  }
}
